export function adapter() {
    let movedToSlideHeader = undefined
    function moveReviewerName() {
        const reviewSlides = document.querySelectorAll(".client-reviews .slide")
        if (document.body.clientWidth <= 800) {
            if (movedToSlideHeader === undefined || movedToSlideHeader === false) {
                for (const slide of reviewSlides) {
                    const rating = slide.querySelector(".slide__rating-link")
                    const slideHeader = slide.querySelector(".slide__header")
                    const reviewerImg = slide.querySelector(".slide__reviewer-img")
                    const name = slide.querySelector(".slide__reviewer-name")
                    slideHeader.insertAdjacentElement("afterbegin", reviewerImg)
                    rating.insertAdjacentElement("beforebegin", name)
                }
                movedToSlideHeader = true
            }
        } else {
            if (movedToSlideHeader === undefined || movedToSlideHeader === true) {
                for (const slide of reviewSlides) {
                    const reviewer = slide.querySelector(".slide__reviewer")
                    const name = slide.querySelector(".slide__reviewer-name")
                    const reviewerImg = slide.querySelector(".slide__reviewer-img")
                    reviewer.insertAdjacentElement("beforeend", name)
                    reviewer.insertAdjacentElement("afterbegin", reviewerImg)
                }
                movedToSlideHeader = false
            }
        }
    }
    document.addEventListener("DOMContentLoaded", moveReviewerName)
    window.addEventListener("resize", moveReviewerName)
}
