export class VideoSourceSelector {
    constructor(selector) {
        const video = document.querySelector(selector)
        this.order = ["video/webm", "video/mp4"] // breakpoint sort order
        this.breakpoints = {}

        video.querySelectorAll("[data-src]").forEach((source) => {
            const dataMw = source.getAttribute("data-mw")
            if (!dataMw) {
                throw new Error(`unable to find data-mw attribute`)
            }
            if (isNaN(dataMw)) {
                throw new Error(`data-mw is expected to be a number, '${dataMw}' found`)
            }
            if (!this.breakpoints[dataMw]) this.breakpoints[dataMw] = []
            this.breakpoints[dataMw].push({
                src: source.getAttribute("data-src"),
                type: source.getAttribute("type"),
            })
        })
        video.innerHTML = "" // we clean up so that there is nothing superfluous

        this.chooseSource()
        this.listenToResizes()
    }

    listenToResizes() {
        window.addEventListener("resize", this.chooseSource.bind(this))
    }

    chooseSource() {
        const windowWidth = window.innerWidth // document.body.clientWidth
        const widthNow = video.getAttribute("data-width-now") || null
        //   console.log("breakpoints", this.breakpoints)
        const numericBreakpoints = Object.keys(this.breakpoints)
            .filter((s) => !isNaN(s))
            .map(Number)
        let filteredBreakpoints = numericBreakpoints.filter((key) => key >= windowWidth)

        //   console.log("filtered", filteredBreakpoints)
        let targetBreakpoint =
            filteredBreakpoints.length === 0
                ? Math.max(...numericBreakpoints)
                : Math.min(...filteredBreakpoints)
        //   console.log(targetBreakpoint)

        if (widthNow && widthNow == targetBreakpoint) return

        video.setAttribute("data-width-now", targetBreakpoint)
        video.innerHTML = ""
        //   console.log(this.breakpoints[targetBreakpoint])
        this.sortBreakpointsByType(this.breakpoints[targetBreakpoint])
        for (const breakpoint of this.breakpoints[targetBreakpoint]) {
            const source = document.createElement("source")
            source.src = breakpoint.src
            source.type = breakpoint.type
            video.append(source)
        }
        //   video.load()
    }
    sortBreakpointsByType(breakpoint) {
        // types that are not included in the order array will go to the bottom
        breakpoint.sort((a, b) => {
            if (this.order.includes(a.type) && this.order.includes(b.type)) {
                return this.order.indexOf(a.type) - this.order.indexOf(b.type)
            } else if (this.order.includes(a.type) && !this.order.includes(b.type)) {
                return -1
            } else {
                return 1
            }
        })
    }
}
