import { debounce } from "./functions.js"

export function textSlider() {
    const articleBodySelector = ".article__body"
    const textPreviewSelector = ".article__text-preview"
    const articleBodies = document.querySelectorAll(articleBodySelector)
    for (const articleBody of articleBodies) {
        processText(articleBody)
    }
    function processText(container) {
        let activeArticle
        let activeTextPreview
        let activeTitle
        let activeContentContainer
        let articleContent
        let touched = false
        container.addEventListener("touchstart", () => (touched = true), { passive: true })
        container.addEventListener("mouseenter", slideUp)
        container.addEventListener("mouseleave", slideDown)
        //   container.addEventListener("click", () => activeTextPreview.click())
        function setupVars(e) {
            activeArticle = e.target.closest(articleBodySelector)
            activeTextPreview = activeArticle.querySelector(textPreviewSelector)
            activeContentContainer = activeArticle.querySelector(".article__content-container")
            activeTitle = activeArticle.querySelector(".article__title")
            articleContent = activeArticle.querySelector(".article__content")
        }
        function slideUp(e) {
            setupVars(e)
            if (touched) return
            articleContent.style.transform = "translateY(0)"
            activeContentContainer.style.backgroundColor = "rgba(0, 0, 0, 0.6)"
        }
        function slideDown() {
            if (!activeArticle) return
            touched = false
            articleContent.style.transform = `translateY(calc(100% - ${activeTitle.offsetHeight}px)`
            activeContentContainer.style.backgroundColor = ""
        }
    }
    function hideTextPreview() {
        for (const target of document.querySelectorAll(textPreviewSelector)) {
            setMargin(target)
        }
    }
    function setMargin(target) {
        const paddingBottom = parseInt(window.getComputedStyle(target.parentElement).paddingBottom)
        let activeArticle = target.closest(articleBodySelector)
        let activeTitle = activeArticle.querySelector(".article__title")
        let articleContent = activeArticle.querySelector(".article__content")
        const titleHeight = activeTitle.offsetHeight
        articleContent.style.transform = `translateY(calc(100% - ${titleHeight + paddingBottom}px)`
    }

    window.addEventListener("resize", debounce(hideTextPreview, 200))
    document.addEventListener("DOMContentLoaded", hideTextPreview)
}
