initBurger()

import { init } from "./modules/tabs.js"
init()

import { isWebp } from "./modules/functions.js"
isWebp()

import { initFloatingNav } from "./modules/header.js"
initFloatingNav()

import { textSlider } from "./modules/text-slider.js"
textSlider()

import { initFooter } from "./modules/header.js"
initFooter()

import { DynamicAdapt } from "./modules/dynamic-adapt.js"
new DynamicAdapt("max").init()

import { initSpoilers } from "./modules/spoiler.js"
document.addEventListener("DOMContentLoaded", initSpoilers)

import { initPopups } from "./modules/popup.js"
initPopups()

import { loadScript } from "./modules/functions.js";
if (document.querySelector('.range-slider')) {
    new LazyLoad({
        elements_selector: ".range-slider",
        callback_enter: () => {
            loadScript('js/nouislider.min.js')
        }
    })
}
if (document.querySelector('.popular__cards-list')) {
    loadScript('js/cards.min.js')
}
