import JustValidate from "just-validate"

const defaultErrorColor = "#ff831e"
export function initSearch() {
    document.addEventListener("click", (e) => {
        const search = document.querySelector(".search-popup")
        if (!search) return
        const input = search.querySelector("input[name=search]")
        const hint = e.target.closest(".search__hint")
        if (hint) {
            input.value = hint.textContent.trim() + " "
        } else {
            const clearInputBtn = e.target.closest(".search__clear-input")
            if (clearInputBtn) {
                input.value = ""
            }
        }
    })
    const searchForm = document.querySelector(".search__form")
    if (searchForm) searchForm.addEventListener("submit", submitSearch)
    function submitSearch(e) {
        e.preventDefault()
    }
    const validateCallback = new JustValidate(".search-popup", {
        validateBeforeSubmitting: false,
        errorLabelStyle: {
            color: defaultErrorColor,
        },
    })

    validateCallback.addField(
        ".search__user-input",
        [
            {
                rule: "required",
                errorMessage: ",       ",
            },
        ],
        { errorsContainer: ".search__error-container" }
    )
}
