export function video() {
    const videoContainer = document.querySelector(".video")
    if (videoContainer) {
        const video = videoContainer.querySelector(".video__video")
        const btn = videoContainer.querySelector(".video-play-pause-btn")
        ;["loadeddata", "play", "pause", "ended"].forEach((event) => {
            // console.log("event", event)
            video.addEventListener(event, (e) => togglePlayBtn(video))
        })

        videoContainer.addEventListener("click", (e) => {
            if (video.paused || video.ended) {
                video.play()
            } else {
                video.pause()
            }
        })
        function togglePlayBtn(video) {
            // btn.classList.toggle("video-play-pause-btn--hidden")
            if (video.paused || video.ended) {
                btn.classList.remove("video-play-pause-btn--hidden")
            } else {
                btn.classList.add("video-play-pause-btn--hidden")
            }
        }
    }
}

