const activeTabClassName = "tab--active"
const activeButtonClassName = "button--active"

const tabButtonBar = document.querySelectorAll(".tab-bar")
tabButtonBar.forEach((bar) => bar.addEventListener("click", (e) => switchButton(e.target)))
export function init() {
    document.addEventListener("DOMContentLoaded", () => {
        const tabButtonBar = document.querySelectorAll(".tab-bar")
        tabButtonBar.forEach((bar) => initTabs(bar))
    })
}
function switchButton(el) {
    const activeTabButton = el.closest("button")
    doSwitch(activeTabButton)
}
function initTabs(tabBar) {
    const activeTabButton = tabBar.querySelector("[data-tab-active]")
    doSwitch(activeTabButton)
}
function doSwitch(activeTabButton) {
    const tabBar = activeTabButton.closest(".tab-bar")
    const tabsContainer = tabBar.closest("[data-tabs-container]")
    const tabsHolder = Array.prototype.filter.call(tabsContainer.children, (c) =>
        c.matches("[data-tabs]")
    )[0]
    const tabs = Array.prototype.filter.call(tabsHolder.children, (c) => c.matches("[data-tab]"))
    const tabIndex = activeTabButton.dataset.tabIndex
    const buttons = Array.prototype.filter.call(tabBar.children, (c) => c.matches("button"))
    resetActiveTab(tabs, tabIndex, activeTabClassName)
    resetActiveButton(activeTabButton, buttons)
}
// const FADE_TIMEOUT = 1000
const tabEl = document.querySelector(".tab")
function resetActiveTab(tabs, tabIndex) {
    let tabFadeTimeout = getComputedStyle(tabEl).transitionDuration
    const to = parseFloat(tabFadeTimeout) * 1000
    tabs.forEach((tab) => {
        if (tab.classList.contains(activeTabClassName)) {
            tab.classList.add("tab--is-fading-out")
            tab.classList.remove(activeTabClassName)
            setTimeout(() => {
                tab.classList.remove("tab--is-fading-out")
            }, to)
        }
    })
    if (tabs[tabIndex - 1]) {
        const activeTab = tabs[tabIndex - 1]

        // activeTab.classList.add("tab--is-fading-in")
        setTimeout(() => {
            activeTab.classList.add(activeTabClassName)
            // activeTab.classList.remove("tab--is-fading-in")
        }, to)
    }
}
function resetActiveButton(tabButton, buttons) {
    buttons.forEach((button) => {
        button.classList.remove(activeButtonClassName)
        button.removeAttribute("data-tab-active")
        // button.dataset.tabActive = false
    })
    tabButton.classList.add(activeButtonClassName)
    tabButton.dataset.tabActive = true
}
