const burgerSelector = ".burger"
const burger = document.querySelector(burgerSelector)
const burgerOpenClassName = "popup--open"
const openBtn = document.querySelector(".main-header__burger-button")
const selectedItem = document.querySelector(".burger__selected-main-item")
const secondaryListContainer = document.querySelector(".burger__secondary-list-container")

export function initBurger() {
    const burgerFunction = (e) => {
        const link = e.target.closest(".burger-link")
        if (link) {
            const secondaryMenu = link
                .closest(".burger-list-item")
                .querySelector(".burger-link-list")
            if (!link.classList.contains(".burger-link--active")) {
                for (const child of link.closest(".burger-link-list").children) {
                    child.querySelector(".burger-link").classList.remove("burger-link--active")
                }
                link.classList.add("burger-link--active")
                selectedItem.textContent = link.textContent
            }
            if (secondaryMenu) {
                if (e.target.closest(".burger__main-link ")) {
                    while (secondaryListContainer.firstChild) {
                        secondaryListContainer.lastChild.remove()
                    }
                }
                const clone = secondaryMenu.cloneNode(true)
                if (!clone.classList.contains("burger-link-list--open")) {
                    clone.classList.add("burger-link-list--open")
                }
                secondaryListContainer.insertAdjacentElement("afterbegin", clone)
            } else {
                if (e.target.closest(".burger__main-link ")) {
                    while (secondaryListContainer.firstChild) {
                        secondaryListContainer.lastChild.remove()
                    }
                }
            }
        }
        e.preventDefault()
        if (e.target.closest(".burger__main-link")) {
            burger.closest(".popup").scrollTo(0, 0)
        }
    }
    const keyBurgerFunction = (e) => {
        const link = e.target.closest(".burger__main-link ")
        if (link) {
            if (e.code === "Space" || e.code === "Enter") {
                burgerFunction(e)
            } else if (e.code === "ArrowRight") {
                const secondaryMenuLink = document.querySelector(
                    ".burger__secondary-list-container .burger__secondary-list a"
                )
                console.log("a", secondaryMenuLink)
                secondaryMenuLink.focus({ focusVisible: true })
            }
        } else {
            const link = e.target.closest(".burger__secondary-link")
            if (link) {
                if (e.code === "ArrowLeft") {
                    const activeLink = document.querySelector(".burger-link--active")
                    activeLink.focus()
                }
            }
        }
    }
    let burgerAttached = false
    const attachBurgerListener = (e) => {
        if (document.body.clientWidth >= 700) {
            if (!burgerAttached) {
                burger.addEventListener("click", burgerFunction)
                document.addEventListener("keydown", keyBurgerFunction)
                burgerAttached = true
            }
        } else {
            if (burgerAttached) {
                burger.removeEventListener("click", burgerFunction)
                document.removeEventListener("keydown", keyBurgerFunction)
                burgerAttached = false
            }
        }
    }
    window.addEventListener("resize", attachBurgerListener)
    document.addEventListener("DOMContentLoaded", attachBurgerListener)
}
