const mainHeader = document.querySelector(".main-header")
function toggleHeaderPosition(e) {
    if (window.scrollY >= 70) {
        if (!mainHeader.classList.contains("main-header--sticky")) {
            mainHeader.classList.add("main-header--sticky")
            mainHeader.style.top = "0"
        }
    } else {
        if (mainHeader.classList.contains("main-header--sticky")) {
            mainHeader.classList.remove("main-header--sticky")
        }
        mainHeader.style.top = `${90 - window.scrollY}px`
    }
}
const FLOATING_NAV_CLASS = "floating-nav"
const FLOATING_NAV_SELECTOR = "." + FLOATING_NAV_CLASS
const FLOATING_NAV_OPEN = FLOATING_NAV_CLASS + "--open"

const floatingNav = document.querySelector(FLOATING_NAV_SELECTOR)
function toggleFloatingNav() {
    if (window.scrollY >= 70) {
        if (!floatingNav.classList.contains(FLOATING_NAV_OPEN)) {
            floatingNav.classList.add(FLOATING_NAV_OPEN)
        }
    } else {
        floatingNav.classList.remove(FLOATING_NAV_OPEN)
    }
}

export function initFloatingNav() {
    document.addEventListener("scroll", (e) => {
        toggleFloatingNav()
    })
    document.addEventListener("DOMContentLoaded", (e) => {
        toggleFloatingNav()
    })
}
export function initFooter() {
    document.querySelectorAll(".footer__go-up").forEach((el) =>
        el.addEventListener("click", (e) => {
            window.scrollTo(0, 0)
        })
    )
}
document.addEventListener("DOMContentLoaded", (e) => {
    const menuList = document.querySelector(".main-menu__list")
    if (menuList) menuList.style.opacity = 1
})
